import { buildUrl } from '@/common/utill/api';
import axios from 'axios';
import {
  getWebtoonInfoParam,
  getSearchWebtoonInfoParam,
  webtoonInfo,
} from '@/types/webtoon';

const BASE_URL = 'https://korea-webtoon-api.herokuapp.com';

async function fetchAPI<T>(apiUrl: string, params: object): Promise<T> {
  try {
    const url = buildUrl(apiUrl, params);
    const response = await axios.get<T>(url);
    return response.data;
  } catch (error) {
    console.error('API call failed:', error);
    throw error;
  }
}

/**
 *   API
 * @param param {Object}
 * @param param.page {number}  
 * @param param.perPage {number}    
 * @param param.service {number}  
 * @param param.updateDay {string}   
 * @returns
 */
export const getWebtoonInfo = (params: getWebtoonInfoParam) => {
  return fetchAPI<webtoonInfo>(BASE_URL, params);
};

/**
 *    API
 * @param param {Object}
 * @param param.keyword {string} 	 
 * @returns
 */
export const getSearchWebtoonInfo = (params: getSearchWebtoonInfoParam) => {
  return fetchAPI<webtoonInfo>(`${BASE_URL}/search`, params);
};
